import React, { useContext, useState } from 'react';

import {
    Chart,
    ChartLegend,
    ChartSeries,
    ChartSeriesItem,
    ChartSeriesLabels,
    ChartSeriesItemTooltip,
    ChartTooltip
  } from "@progress/kendo-react-charts";
  import "hammerjs";
import { Button, ButtonGroup, Grid } from '@material-ui/core';
import { Context as AuthContext } from '../context/AuthContext';


  export default function Donut(){
      const fuel_data = [ {
        "kind": "Petrol", "share": 0.175, "color": "#f71670"
    }, 
    {
        "kind": "Diesel", "share": 0.238, "color": "#4287f5"
    },
    {
        "kind": "CNG", "share": 0.118, "color": "#83e077"
    }, 
];
const test_data = [ {
  "kind": "Pass", "share": 0.375, "color": "#83e077"
}, 
{
  "kind": "Fail", "share": 0.238, "color": "#ff3838"
},
];
const vehicle_data = [ {
  "kind": "BSI", "share": 0.175, "color": "#83e077"
}, 
{
  "kind": "BSII", "share": 0.238, "color": "#83e07799"
},
{
  "kind": "BSIII", "share": 0.118, "color": "#83e07790"
}, 
{
  "kind": "BSIV", "share": 0.118, "color": "#83e07780"
}, 
{
  "kind": "BSV", "share": 0.118, "color": "#83e07770"
}, 
{
  "kind": "BSVI", "share": 0.118, "color": "#83e07760"
}, 
];
const { state: { mode }, signin } = useContext(AuthContext);
const [show, setShow] = useState('Fuel');

    const labelContent = (e) => e.category;
      return (
          <div>
              <div style={{ height: '70px', width: '95%',textAlign: 'center', margin: '0 auto', justifyContent: 'center', alignItems: 'center', backgroundColor: mode==='Light Mode' ? '#F3F3F500' : '#25252600' }}>
                    <ButtonGroup  style={{ margin: '0 auto', marginTop: '10px', }} color="primary" aria-label="outlined primary button group">
                        <Button onClick={()=>{
                            //
                            setShow('Fuel');
                        }} style={{ backgroundColor: show==='Fuel' ? '#252526' : '#1e1e1e', color: '#f3f3f599', borderColor: '#252525', fontSize: '12px' }}>   Fuel   </Button>
                        <Button onClick={()=>{
                            //
                            setShow('Test Result');
                        }} style={{ backgroundColor: show==='Test Result' ? '#252526' : '#1e1e1e', color: '#f3f3f599', borderColor: '#252525', fontSize: '12px' }}>Test</Button>
                        <Button onClick={()=>{
                            //
                            setShow('Vehicle');
                        }} style={{ backgroundColor: show==='Vehicle' ? '#252526' : '#1e1e1e', color: '#f3f3f599', borderColor: '#252525', fontSize: '12px' }}>Emission Norms</Button>
                    </ButtonGroup>
                </div>
                <div style={{ height: '50%', width: '100%',textAlign: 'center', margin: '0 auto', justifyContent: 'center', alignItems: 'center', backgroundColor: mode==='Light Mode' ? '#F3F3F500' : '#1e1e1e00', marginTop: '10px' }}>
                    <Chart>
                    <ChartTooltip />
                        <ChartSeries>
                            <ChartSeriesItem size={50}
                                type="donut"
                                data={show==='Fuel' ? fuel_data : show==='Vehicle' ? vehicle_data : test_data}
                                categoryField="kind"
                                field="share">
                                <ChartSeriesLabels
                                color="#ffffff"
                                background='none'
                                content={labelContent}
                                />
                                <ChartSeriesItemTooltip color={'#f3f3f599'} background="transparent"/>
                            </ChartSeriesItem>
                            </ChartSeries>
                        <ChartLegend visible={false} />
                    </Chart>
                </div>
          </div>
      )
  }