import React, { useContext, useState } from 'react';

import {
    Chart,
    ChartTitle,
    ChartSeries,
    ChartSeriesItem,
    ChartCategoryAxis,
    ChartCategoryAxisTitle,
    ChartCategoryAxisItem,
    ChartTooltip,
    ChartSeriesItemTooltip
  } from "@progress/kendo-react-charts";
  import "hammerjs";
import { Button, ButtonGroup, Grid } from '@material-ui/core';
import { Context as AuthContext } from '../context/AuthContext';


  export default function TBar(){
    const [show, setShow] = useState('No. of Active Cases / Day');
  
const [daily] = [
    [100, 123, 234, 343, 220, 270, 40],
  ];
  const [monthly] = [
      [100, 123, 234, 343, 220, 270, 40, 234, 343, 220, 270, 40],
    ];
    const [yearly] = [
        [100, 123],
      ];
  const daily_categories = [27, 26, 25, 24, 23, 22, 22];
  const monthly_categories = ['Jul', 'Jun', 'May', 'Apr', 'Mar', 'Feb', 'Jan', 'Dec', 'Nov', 'Oct', 'Sep', 'Aug'];
  const yearly_categories = ['2021', '2020', '2019'];
const { state: { mode }, signin } = useContext(AuthContext);

      return (
          <div>
              <div style={{ height: '70px', width: '100%',textAlign: 'center', margin: '0 auto', justifyContent: 'center', alignItems: 'center', backgroundColor: mode==='Light Mode' ? '#F3F3F500' : '#25252600' }}>
                    <ButtonGroup  style={{ margin: '0 auto', marginTop: '10px', width: '95%' }} color="primary" aria-label="outlined primary button group">
                        <Button onClick={()=>{
                            //
                            setShow('No. of Active Cases / Day');
                        }} style={{ backgroundColor: show==='No. of Active Cases / Day' ? '#252526' : '#1e1e1e', color: '#f3f3f599', borderColor: '#252525', width: '40%', fontSize: '12px' }}>Last 7 days</Button>
                        <Button onClick={()=>{
                            //
                            setShow('No. of Active Cases / Month');
                        }} style={{ backgroundColor: show==='No. of Active Cases / Month' ? '#252526' : '#1e1e1e', color: '#f3f3f599', borderColor: '#252525', width: '30%', fontSize: '12px' }}>Monthly</Button>
                        <Button onClick={()=>{
                            //
                            setShow('No. of Active Cases / Year');
                        }} style={{ backgroundColor: show==='No. of Active Cases / Year' ? '#252526' : '#1e1e1e', color: '#f3f3f599', borderColor: '#252525', width: '30%', fontSize: '12px' }}>Yearly</Button>
                    </ButtonGroup>
                </div>
                <div style={{ height: '50%', width: '100%', margin: '0 auto', backgroundColor: '#1e1e1e00', marginTop: '10px' }}>
                    <Chart style={{ height: '100%', width: '90%',margin: '0 auto',  }}>
                        <ChartTitle color='transparent' text="Units sold" />
                        <ChartCategoryAxis >
                        <ChartCategoryAxisItem color='#4287f500'   background='transparent' type='category' autoBaseUnitSteps='fit' categories={show==='yearly' ? yearly_categories : show==='monthly' ? monthly_categories : daily_categories}>
                            <ChartCategoryAxisTitle color='#fafafa40'   text={show} />
                        </ChartCategoryAxisItem>
                        </ChartCategoryAxis>
                        <ChartTooltip />
                        <ChartSeries>
                        <ChartTooltip color='red' render={()=>{
                            return (
                                <div>
                                    {show==='No. of Active Cases / Year' ? yearly_categories : show==='No. of Active Cases / Month' ? monthly_categories : daily_categories}
                                </div>
                            )
                        }} />

                        <ChartSeriesItem field='order' a size='90px' type="bar" spacing={30} color='#83e077'  data={show==='No. of Active Cases / Year' ? yearly : show==='No. of Active Cases / Month' ? monthly : daily}>
                            <ChartSeriesItemTooltip color={'#f3f3f599'} background="transparent"/>
                        </ChartSeriesItem>
                        </ChartSeries>
                    </Chart>
                </div>
          </div>
      )
  }