import React, { useContext, useState } from 'react';

import {
    Chart,
    ChartLegend,
    ChartSeries,
    ChartSeriesItem,
    ChartSeriesItemTooltip,
    ChartSeriesLabels,
  } from "@progress/kendo-react-charts";
  import "hammerjs";
import { Button, ButtonGroup, Grid } from '@material-ui/core';
import { Context as AuthContext } from '../context/AuthContext';


  export default function TDonut(){
const test_data = [ {
  "kind": "Closed", "share": 0.375, "color": "#83e077"
}, 
{
  "kind": "Open", "share": 0.238, "color": "#ff3838"
},
];
const { state: { mode }, signin } = useContext(AuthContext);
const [show, setShow] = useState('Cases');

    const labelContent = (e) => e.category;
      return (
          <div>
              <div style={{ height: '70px', width: '100%',textAlign: 'center', margin: '0 auto', justifyContent: 'center', alignItems: 'center', backgroundColor: mode==='Light Mode' ? '#F3F3F500' : '#25252600' }}>
                    <ButtonGroup  style={{ width: '70%', margin: '0 auto', marginTop: 20, }} color="primary" aria-label="outlined primary button group">
                        <Button onClick={()=>{
                            //
                            setShow('Cases');
                        }} style={{ width: '60%', backgroundColor: show==='Cases' ? '#252526' : '#1e1e1e', color: '#f3f3f599', borderColor: '#252526', margin: '0 auto' }}>   Cases   </Button>
                    </ButtonGroup>
                </div>
                <div style={{ height: '50%', width: '100%',textAlign: 'center', margin: '0 auto', justifyContent: 'center', alignItems: 'center', backgroundColor: mode==='Light Mode' ? '#F3F3F500' : '#1e1e1e00', marginBottom: 20, marginTop: 20 }}>
                    <Chart>
                        <ChartSeries>
                            <ChartSeriesItem size={50}
                                type="donut"
                                data={test_data}
                                categoryField="kind"
                                field="share">
                                <ChartSeriesLabels
                                color="#f3f3f5"
                                background='none'
                                content={labelContent}
                                />
                                <ChartSeriesItemTooltip color={'#f3f3f599'} background="transparent"/>
                            </ChartSeriesItem>
                            </ChartSeries>
                        <ChartLegend visible={false} />
                    </Chart>
                </div>
          </div>
      )
  }