import React, { useContext, useEffect, useState } from 'react';
import { Navbar, Nav } from 'react-bootstrap';
import { Context as AuthContext } from '../context/AuthContext';
import {
    BrowserRouter as Router,
    Switch as RSwitch,
    Route,
    Link
  } from "react-router-dom";
import About from './About';
import Home from './Home';
import Login from './Login';
import { Button, ButtonGroup, Switch, withStyles } from '@material-ui/core';
import { purple } from '@material-ui/core/colors';
import RoomIcon from '@material-ui/icons/Room';

export default function NavBar(scrolled){
    //
    const { state: { mode, show }, changeToDarkMode, changeToLightMode } = useContext(AuthContext);
    const [navColor, setNavColor] = useState('#1e1e1e');
    const [aboutTab, setAboutTab]= useState(false);
    const [homeTab, setHomeTab] = useState(true);
    const { state : { token, profile, }, getProfile, signout } = useContext(AuthContext);
 //   const [show, setShow] = useState(profile!=null && profile.role==='DEALER' ? 'PUC' : null);
  //  const prevScroll = window.pageYOffset;
    //const [visible, setVisible] = useState(true);
    //const [scrollPos, setScrollPos] = useState(0);
/*
    function HandleScroll(){
        //const currentScroll = window.pageYOffset;
        //setVisible(prevScroll>currentScroll);

        setScrollPos(document.body.getBoundingClientRect().top);
        setVisible(document.body.getBoundingClientRect().top > scrollPos)
    }

    useEffect(()=>{
        window.addEventListener('scroll', HandleScroll);

        return ()=> {
            window.removeEventListener('scroll', HandleScroll);
        }
    },[visible])
*/


useEffect(() => {
    //
    console.log('hi 1')
    if(token===null && profile===null){
        //
        console.log('hi 2')
        getProfile()
    }
}, [mode, token, profile]);


const PurpleSwitch = withStyles({
    switchBase: {
      color: mode==='Dark Mode' ? '#252526' : '#ffffff',
      '&$checked': {
        color: mode==='Dark Mode' ? '#252526' : '#ffffff',
      },
      '&$checked + $track': {
        backgroundColor: mode==='Dark Mode' ? '#fafafa' : '#fafafa',
      },
    },
    checked: {},
    track: {},
  })(Switch);






    return (
        <Router>
            <div>
                <Navbar hidden={ profile!=null && profile.role==='RTO' ? true : true } /* hidden={token!=null ? false : true}*/ className="navbar-header" 
                style={{ backgroundColor: mode==='Light Mode' ? token!=null ? '#F3F3F5' : '#F3F3F5' : token!=null ? '#252526' : '#252526' }}>
                    <Navbar.Brand>
                        {token!=null ?
                        <img
                            alt=""
                            src='https://www.tapeit.in/assets/images/logoNew1.png'
                            width="22%"
                            height="11%"
                            className="d-inline-block align-top"
                        /> : null}
                    </Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="ml-left">
                            {token===null ?
                            (<Nav.Link onClick={()=>{
                                setNavColor('#ff3838');
                                //remove token from state
                                setHomeTab(false);
                                setAboutTab(true);
                            }} style={{ color: '#ffffff', fontSize: '15px' }}></Nav.Link>
                            ) : null}
                        </Nav>
                        {token!=null && profile!=null ?
                        <ButtonGroup  style={{ margin: '0 auto', marginTop: '1px', }} color="primary" aria-label="outlined primary button group">
                        <Button onClick={()=>{
                            //
                          //  setShow('PUC');
                            //pucModal()
                        }} style={{ width: '200px', backgroundColor: show==='PUC' ? '#1e1e1e' : '#1e1e1e', color: '#f3f3f599', borderColor: '#1e1e1e' }}>Home</Button>
                        <Button onClick={async()=>{
                            //
                          //  await getBar('MONTH');
                            //await getDonut();
                           // await setShow('Reports');
                        }} style={{ width: '200px', backgroundColor: show==='Reports' ? '#1e1e1e' : '#252526', color: '#f3f3f599', borderColor: '#1e1e1e' }}>Transactions</Button>
                    </ButtonGroup> : null}
                        <Nav className="ml-auto">
                            {token!=null ?
                            <span onClick={()=>{
                                //
                                signout()
                            }} style={{ color: '#ffffff90', marginTop: '7px', marginRight: '10px' }}>Log Out</span> : null}
                        </Nav>
                    </Navbar.Collapse>
                </Navbar>
            </div>
            <div>
                <RSwitch>
                    <Route path="/">
                        {token!=null && profile!=null ?
                        <Home /> : <Login />}
                    </Route>
                </RSwitch>
            </div>
        </Router>
    )
}

