import React, { useContext, useEffect, useRef, useState } from 'react';

import { Context as AuthContext } from '../context/AuthContext';
import { Grid, } from '@material-ui/core';

import CreditsTable from './CreditsTable';
import ManageOperator from './ManageOperator';
import PuccTable from './PuccTable';
import RtaHome from './RtaHome';
import ManageRta from './ManageRta';

//https://newmwebasset.s3.ap-south-1.amazonaws.com/tdlogo.gif
//https://newmwebasset.s3.ap-south-1.amazonaws.com/Integer-Logo.jpg

export default function Admin(){
    const { state: { creditsTransactions, puccTransactions, mode, profile, co, hc, o2, co2, token, paymentLink, pmodal, verified, vehicle, bar, donut, show, otpsuccess, certificate, reading },fetchCreditsTransactions, fetchPuccTransactions, buyCredits, getCertificate, getReadings, refreshPuc, signin, getOtp, testToken, pucModal, verifyOtp, getBar, getDonut, signout } = useContext(AuthContext);
    console.log('profile is: ',profile );
    console.log('mode from home is', mode);
    const [current, setCurrent] = useState('Dashboard');


    return(
        <div className='signup-form'>
            <div style={{ height: '10px', width: '100%',textAlign: 'center', margin: '0 auto', justifyContent: 'center', alignItems: 'center', backgroundColor: '#25252699', }}></div>
                <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', flexDirection: 'row', height: '1000px', width: '100%', backgroundColor: '#25252699', margin: '0 auto' }}>
                    <div style={{ height: '800px', width: '90%', margin: '0 auto', backgroundColor: '#ffffff', flexDirection: 'row', borderRadius: 15 }}>
                        <Grid container>
                            <Grid style={{ display: 'flex', flexDirection: 'column', justifyContent: 'space-around', alignItems: 'center', backgroundColor: '#3455eb', height: '800px', borderTopLeftRadius: 15, borderBottomLeftRadius: 15 }} item xs={3}>
                                <div onClick={()=>{
                                    //
                                    //setManageModal(true)
                                    setCurrent('Dashboard')
                                }} style={{ textAlign: 'center', color: current==='Dashboard' ? '#ffffff99' : 'white', fontSize: '20px', fontWeight: 'bolder' }}>
                                    <img 
                                        alt=""
                                        src='https://newmwebasset.s3.ap-south-1.amazonaws.com/tdlogo.gif'
                                        width="50px"
                                        height="60px"
                                        style={{  marginBottom: '15px'  }}
                                        className="d-inline-block align-top" /><br />
                                    Dashboard
                                </div>
                                <div onClick={()=>{
                                    //
                                    //setManageModal(true)
                                    setCurrent('Manage RTA')
                                }} style={{ textAlign: 'center', color: current==='Manage RTA' ? '#ffffff99' : 'white', fontSize: '20px', fontWeight: 'bolder' }}>
                                    Manage<br />RTA
                                </div>
                                <div onClick={()=>{
                                    //
                                    //setManageModal(true)
                                    setCurrent('Manage Operator')
                                }} style={{ textAlign: 'center', color: current==='Manage Operator' ? '#ffffff99' : 'white', fontSize: '20px', fontWeight: 'bolder' }}>
                                    Manage<br />Operator
                                </div>
                                <div onClick={()=>{
                                    //
                                    //setManageModal(true)
                                    setCurrent('Credits Transactions')
                                    fetchCreditsTransactions()
                                }}  style={{ textAlign: 'center', color: current==='Credits Transactions' ? '#ffffff99' : 'white', fontSize: '20px', fontWeight: 'bolder' }}>
                                    Credits<br />Transactions
                                </div>
                                <div onClick={()=>{
                                    //
                                    //setManageModal(true)
                                    setCurrent('Pucc Transactions')
                                    fetchPuccTransactions()
                                }}  style={{ textAlign: 'center', color: current==='Pucc Transactions' ? '#ffffff99' : 'white', fontSize: '20px', fontWeight: 'bolder' }}>
                                    Pucc<br />Transactions
                                </div>
                            </Grid>
                            <Grid style={{ backgroundColor: '#ffffff', height: '800px', borderTopRightRadius: 15, borderBottomRightRadius: 15 }} item xs={9}>
                                <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', height: '100px', backgroundColor: 'transparent', borderTopRightRadius: 15 }}>
                                    <span>
                                        <span style={{ marginLeft: '30px', color: '#000000', fontSize: '28px', fontWeight: 'bolder', }}></span>
                                    </span>
                                    <h5 onClick={()=>{
                                        //
                                        signout()
                                    }} style={{ marginRight: '25px' }}>Log Out</h5>
                                </div>
                                {current==='Dashboard' ?
                                (<RtaHome />) : null}
                                {current==='Manage RTA' ?
                                (<ManageRta />) : null}
                                {current==='Manage Operator' ?
                                (<ManageOperator />) : null}
                                {current==='Credits Transactions' ?
                                (<div style={{ height: '700px', backgroundColor: '#ffffff', borderBottomRightRadius: 15 }}>
                                    <CreditsTable />
                                </div>) : null}
                                {current==='Pucc Transactions' ?
                                (<div style={{ height: '700px', backgroundColor: '#ffffff', borderBottomRightRadius: 15 }}>
                                    <PuccTable />
                                </div>) : null}
                            </Grid>
                        </Grid>
                    </div>
                </div>
        </div>
    )
}

