const useStyles2 = makeStyles({
  table: {
    minWidth: '90%',
  },
});

export default function Tabe() {
  const classes = useStyles2();
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(6);

  const emptyRows = rowsPerPage - Math.min(rowsPerPage, rows.length - page * rowsPerPage);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  return (
    <TableContainer component={Paper}>
      <Table classdate_time={classes.table} aria-label="custom pagination table">
        <TableBody>
          {(rowsPerPage > 0
            ? rows.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
            : rows
          ).map((row) => (
            <TableRow key={row.date_time}>
              <TableCell style={{ width: '17%' }} component="th" scope="row">
                {row.date_time}
              </TableCell>
              <TableCell style={{ width: '17%' }} align="center">
                {row.vehicle_num}
              </TableCell>
              <TableCell style={{ width: '17%' }} align="center">
                {row.rto_id}
              </TableCell>
              <TableCell style={{ width: '17%' }} align="center">
                {row.operator_location}
              </TableCell>
              <TableCell style={{ width: '17%' }} align="center">
                {row.operator_id}
              </TableCell>
              <TableCell style={{ width: '17%' }} align="center">
                {row.operator_name}
              </TableCell>
              <TableCell style={{ width: '15%' }} align="center">
                {row.test_status}
              </TableCell>
            </TableRow>
          ))}

          {emptyRows > 0 && (
            <TableRow style={{ height: 53 * emptyRows }}>
              <TableCell colSpan={6} />
            </TableRow>
          )}
        </TableBody>
        <TableFooter>
          <TableRow>
            <TablePagination
              rowsPerPageOptions={10}
              colSpan={3}
              count={rows.length}
              rowsPerPage={rowsPerPage}
              page={page}
              SelectProps={{
                inputProps: { 'aria-label': 'rows per page' },
                native: true,
              }}
              onPageChange={handleChangePage}
              onRowsPerPageChange={handleChangeRowsPerPage}
              ActionsComponent={TablePaginationActions}
            />
          </TableRow>
        </TableFooter>
      </Table>
    </TableContainer>
  );
}