export default function AvailableMachines(){
    //

    const [fselectedDate, setFSelectedDate] = useState(Date.now());
    const [tselectedDate, setTSelectedDate] = useState(Date.now());
    const [count, setCount] = useState(null);
    const [searchItemBy, setSearchItemBy] = useState('to present');

    const { machineModelSet } = useContext(AuthContext);

//https://newmwebasset.s3.ap-south-1.amazonaws.com/tdlogo.gif
//https://newmwebasset.s3.ap-south-1.amazonaws.com/Integer-Logo.jpg
 


    function taby(){
        return (
            <div style={{ height: '50px',  }}>
            {machineModels.map((item)=>{
            return (
                    <Grid onClick={()=>{
                        //
                        machineModelSet(item.modelName)
                    }} style={{ width: '95%', margin: '0 auto' }} container>
                        <Grid style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center', height: '50px', backgroundColor: item && item.testStatus==='PASS' ? '#f3f3f599' : '#f3f3f599', marginTop: '5px', borderTopLeftRadius: 30, borderBottomLeftRadius: 30 }} item xs={4}>
                            <div style={{ fontSize: '13px', color: 'black', display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center', height: '50px', backgroundColor: '#f3f3f500', }}>
                                {item.manufactureName}
                            </div>
                        </Grid>
                        <Grid style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center', height: '50px', backgroundColor: item && item.testStatus==='PASS' ? '#f3f3f599' : '#f3f3f599', marginTop: '5px' }} item xs={3}>
                            <div style={{ fontSize: '13px', color: 'black', display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center', height: '50px', backgroundColor: '#f3f3f500', }}>
                                {item.machineType}
                            </div>
                        </Grid>
                        <Grid style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center', height: '50px', backgroundColor: item && item.testStatus==='PASS' ? '#f3f3f599' : '#f3f3f599', marginTop: '5px' }} item xs={3}>
                            <div style={{ fontSize: '13px', color: 'black', display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center', height: '50px', backgroundColor: '#f3f3f500', }}>
                                {item.modelName}
                            </div>
                        </Grid>
                        <Grid style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center', height: '50px', backgroundColor: item && item.testStatus==='PASS' ? '#f3f3f599' : '#f3f3f599', marginTop: '5px', borderTopRightRadius: 30, borderBottomRightRadius: 30 }} item xs={2}>
                            <div style={{ fontSize: '13px', color: 'black', display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center', height: '50px', backgroundColor: '#f3f3f500', }}>
                                <ChevronRightIcon />
                            </div>
                        </Grid>
                    </Grid>
                )
            })
            }
        </div>
        )
    }




    return (
        <div style={{ height: '100%', width: '100%', backgroundColor: '#1e1e1e' }}>
            <div style={{ height: '12%', borderRadius: 15, margin: '0 auto', }}>
                
            </div>
        <div style={{ height: '85%', overflowY: 'scroll' }}>
            <Grid style={{ width: '95%', margin: '0 auto', }} container>
                <Grid style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center', height: '90px', backgroundColor: '#f3f3f599', marginTop: '5px', borderTopLeftRadius: 30, borderBottomLeftRadius: 30 }} item xs={4}>
                    <div style={{ fontSize: '13px', fontWeight: 'bolder', color: 'black', display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center', height: '90px', backgroundColor: '#f3f3f500', }}>
                        {'Manufacturer Name'}
                    </div>
                </Grid>
                <Grid style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center', height: '90px', backgroundColor: '#f3f3f599', marginTop: '5px' }} item xs={3}>
                    <div style={{ fontSize: '13px', fontWeight: 'bolder', color: 'black', display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center', height: '90px', backgroundColor: '#f3f3f500',  }}>
                        {'Machine Type'}
                    </div>
                </Grid>
                <Grid style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center', height: '90px', backgroundColor: '#f3f3f599', marginTop: '5px' }} item xs={3}>
                    <div style={{ fontSize: '13px', fontWeight: 'bolder', color: 'black', display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center', height: '90px', backgroundColor: '#f3f3f500',  }}>
                        {'Machine Model'}
                    </div>
                </Grid>
                <Grid style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center', height: '90px', backgroundColor: '#f3f3f599', marginTop: '5px', borderTopRightRadius: 30, borderBottomRightRadius: 30 }} item xs={2}>
                    <div style={{ fontSize: '13px', fontWeight: 'bolder', color: 'black', display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center', height: '90px', backgroundColor: '#f3f3f500', }}>
                        {''}
                    </div>
                </Grid>
            </Grid>
            {taby()}
        </div>
        </div>
    )
}